import React from 'react';
import {tokens, TokenFormatter} from '../lib/formatting';
import {utils} from 'near-api-js';
import numbro from 'numbro';
import {Tokens} from './icons';
import classNames from 'classnames';

export function AccountStreamCard({
  token,
  balance,
  streamsLength,
  period = '',
  showPeriod = true,
  className,
}) {
  const formatter = TokenFormatter(token);

  let multiplier = 1;
  switch (period) {
    case 'min':
      multiplier = 60;
      break;
    case 'hour':
      multiplier = 60 * 60;
      break;
    case 'day':
      multiplier = 60 * 60 * 24;
      break;
  }

  balance = utils.format.formatNearAmount(balance);

  balance = balance * multiplier;

  return (
    <div
      className={classNames(
        'w-full h-24 rounded-lg bg-input flex items-center width-full p-6',
        className,
      )}
    >
      <div className="w-full flex items-center">
        <div className="w-12 mr-12">
          <span className="flex-shrink-0 rounded-full bg-card2 inline-flex items-center justify-center w-12 h-12">
            {Tokens(token)}
          </span>
        </div>
        <div className="">
          <div className="font-bold">
            {tokens[token].name}, {token}
          </div>
          {streamsLength > 0 ? (
            <div className="text-gray text-sm">from {streamsLength} steams</div>
          ) : (
            ''
          )}
        </div>
        <div className="ml-auto">
          <span className=" text-3xl">
            {balance < 0.001 ? '<0.001' : numbro(balance).format({mantissa: 3})}
          </span>
          {showPeriod ? <span>{period !== '' ? '/' + period : ''}</span> : ''}
        </div>
      </div>
    </div>
  );
}
